.App {
    color: black;
    background-color: white;
    overflow: hidden;
}

.App-header {
    text-align: center;
    background-color: #42A5F5;
    padding: 10px;
    /*position: fixed;*/
    /*transform: translateX(-10%);*/
    width: 100%;
}

.App-title {
    color: white;
    font-size: 1.1rem;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0px;
}

.App-toolbar {
    font-size: large;
    /*position: fixed;*/
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    height: calc(100vh - 41px);
   /* border-bottom: 0.5px solid #AAAAAA; */
}

.App-toolbar-label {
    margin-left: 5px !important;
    font-size: 0.8rem !important;
}

.App-toolbar-field {
    margin: 5px !important;
    border: 0.5px solid #AAAAAA !important;
    font-size: 0.75rem !important;
}

.App-toolbar-button {
    background-color: #04BE02;
    color: #FFF;
    font-size: 1.25rem !important;
    width:  300px;
    line-height:3;
    font-weight: bold;
    margin: 10px;
    cursor: pointer !important;
}

.normal-button {
    background-color: #04BE02;
    color: #FFF;
    font-size: 1.25rem !important;
    max-width:  400px;
    width:100%;
    line-height:2;
    font-weight: bold;
    margin-top: 20px;
    cursor: pointer !important;
}

.App-domain-list {
    overflow-y: scroll;
    overflow-x: hidden;
    position: fixed;
    top: 160px;
    height: -webkit-fill-available;
    height: -moz-available;
    height: stretch;
    width: 100%;
}

.App-domain-list-ul {
    list-style-type: none;
    padding: 5px;
    margin-top: 0px;
}

.App-domain-list-li {
    height: 7.0rem;
    margin-bottom: 30px;
}

.App-domain-item {
    border-bottom: 0.5px solid lightgray;
    padding: 5px 0px 5px 5px;
}

.App-domain-item-select {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-align: center;
        align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    -webkit-box-shadow: 0 0 0 1px #03A9F4;
            box-shadow: 0 0 0 1px #03A9F4;
    position: relative;
    top: -43px;
    text-align: center;
    margin-right: 7px;
    -webkit-transition: border-color .2s easy,-webkit-box-shadow .2s easy;
    transition: border-color .2s easy,-webkit-box-shadow .2s easy;
    -o-transition: border-color .2s easy,box-shadow .2s easy;
    transition: border-color .2s easy,box-shadow .2s easy;
    transition: border-color .2s easy,box-shadow .2s easy,-webkit-box-shadow .2s easy;
}

.App-domain-item-checkmark {
    background-color: #2196F3;
    background-image:url(check-mark.png);
    background-repeat: no-repeat;
    background-position: center;
}

.App-domain-item-body {
    display: inline-block;
    width: 90%;
    overflow: hidden;
    white-space: nowrap;
}

.App-domain-item-title {
    font-size: 0.90rem;
    font-weight: bold;
    margin: 5px;
    white-space: initial;
    max-height: 3.0rem;
    overflow-y: hidden;
}

.App-domain-item-subtitle {
    font-size: 0.75rem;
    color: #42A5F5;
    margin: 5px;
    /*text-overflow: ellipsis;*/
}

.App-domain-item-text {
    font-size: 0.75rem;
    color: #757575;
    margin: 5px;
    /*text-overflow: ellipsis;*/
}

.App-domain-item-highlight-small {
    font-size: 0.70rem;
    color: #81C784;
    margin: 5px;
    /*text-overflow: ellipsis;*/
}

.hide {
    display: none;
}

.QrCode-Scan-Region {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin:20px;
}

.QrCode-Square {
/*    border: 1px solid gray; */
}


